@import "src/styles/mixins";

.form {
    &__container {
        @include flex(column);
        @include gap(30px); }
    &__row, &__column {
        @include flex(row);
        @include gap(30px);
        width: 100%; }
    &__column {
        flex-direction: column; } }

.input, .textarea {
    border-radius: 20px;
    background-color: rgba(255, 255, 255, 0.05);
    padding: 10px 20px;
    width: 100%;
    border: 1px solid transparent;
    &:hover {
        opacity: 0.8; }
    &:focus {
        border: 1px solid rgba(255, 255, 255, 0.1); } }

.textarea {
    height: 150px; }

.button {
    background-color: var(--primaryLightest);
    color: var(--textOnPrimary);
    padding: 10px 20px;
    font-size: 14px;
    align-self: center; }
